import { createReducer, createActions } from 'reduxsauce'
import Immutable from 'seamless-immutable'

import { toggleArray, lodashToggle } from '../../components/utils';


/* ------------- Types and Action Creators ------------- */
const { Types, Creators } = createActions({
    createOrChangeIsubRequest: ['payload'],
    createOrChangeIsubFail: ['payload'],
    createOrChangeIsubSuccess: ['payload'],

    toggleFilterCategories: ['catId'],
})

export const IsubsTypes = Types
export default Creators


/* ------------- Initial State ------------- */
export const INITIAL_STATE = Immutable({
    fetching: false,
    error: null,
    isub: null,
    categories: [],
    filterCategories: [],
    message: '',
})

/* ------------- Selectors ------------- */
export const IsubsSelectors = {
    createOrChangeIsub: state => state.isubs.isub,
}

/* ------------- Reducers ------------- */
// create first job
export const createOrChangeIsubRequest = (state, { payload }) => {
    return state.merge({ fetching: true, message: '' })
}

export const createOrChangeIsubSuccess = (state, { payload }) => {
    return state.merge({ fetching: false, error: null, message: payload.message, isub: payload.isub,
        categories: payload.isub && payload.isub.categories })
}

export const createOrChangeIsubFail = (state, { payload }) => {
    return state.merge({ fetching: false, error: payload, message: '', isub: null, categories: null })
}

export const toggleFilterCategories = (state, { catId }) => {
    let { filterCategories } = state
    filterCategories = lodashToggle(filterCategories, parseInt(catId))
    return state.merge({ filterCategories: filterCategories })
}

/* ------------- Hookup Reducers To Types ------------- */

export const reducer = createReducer(INITIAL_STATE, {
    [Types.CREATE_OR_CHANGE_ISUB_REQUEST]: createOrChangeIsubRequest,
    [Types.CREATE_OR_CHANGE_ISUB_SUCCESS]: createOrChangeIsubSuccess,
    [Types.CREATE_OR_CHANGE_ISUB_FAIL]: createOrChangeIsubFail,

    [Types.TOGGLE_FILTER_CATEGORIES]: toggleFilterCategories,
})
