import { put, call } from 'redux-saga/effects';

import IsubsActions from '../redux-sauce/isubs'

export function* createOrChangeIsub(api, action) {
    try {
        let response = yield call(api.createOrChangeIsub, action.payload);
        yield put(IsubsActions.createOrChangeIsubSuccess(response));
    } catch (err) {
        yield put(IsubsActions.createOrChangeIsubFail(err))
    }
}