import React from 'react';
import ReactDOM from 'react-dom';
// import { browserHistory } from 'react-router';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
} from "react-router-dom";

import { Provider } from 'react-redux';
import configureStore from './redux/store/';

// Containers
import Main from './containers/Main'
import JobFirstCreateShop from './containers/CreateFirstJob'
import ViberAuthenticationComponent from './containers/ViberLogin'
import Header from './containers/HeaderContainer'
import ZakazchikAcc from './containers/ZakazchikAcc'
import JobDetail from './containers/JobDetail'

// components

const store = configureStore()

ReactDOM.render(
  <Provider store={store}>
    <Router>
      {/* Loader Component */}
      {/* <div id="preloader"><div class="spinner-border color-highlight" role="status"></div></div> */}
      <div className='app' id="page">
        <Header />
        {/* <div id="footer-bar" className="footer-bar-5">
          <Link to="/"><i data-feather="heart" data-feather-line="1" data-feather-size="21" data-feather-color="red2-dark" data-feather-bg="red2-fade-light"></i><span>Features</span></Link>
          <Link to="/"><i data-feather="image" data-feather-line="1" data-feather-size="21" data-feather-color="green1-dark" data-feather-bg="green1-fade-light"></i><span>Media</span></Link>
          <Link to="/" className="active-nav"><i data-feather="home" data-feather-line="1" data-feather-size="21" data-feather-color="blue2-dark" data-feather-bg="blue2-fade-light"></i><span>Home</span></Link>
          <Link to="/"><i data-feather="file" data-feather-line="1" data-feather-size="21" data-feather-color="brown1-dark" data-feather-bg="brown1-fade-light"></i><span>Pages</span></Link>
          <Link to="/"><i data-feather="settings" data-feather-line="1" data-feather-size="21" data-feather-color="gray2-dark" data-feather-bg="gray2-fade-light"></i><span>Settings</span></Link>
        </div> */}
        <div className="page-content header-clear">
          <Switch>
            <Route exact path="/">
              <Main />
            </Route>
            <Route path="/zakazchik/create_first/" component={JobFirstCreateShop} />
            <Route path="/zakazchik/account/" component={ZakazchikAcc} />
            <Route path="/dm/login/v/:token" component={ViberAuthenticationComponent} />
            <Route path="/jobs/:id/" component={JobDetail} />
          </Switch>
        </div>
      </div>
    </Router>
  </Provider>, document.getElementById('root')
);