import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  Redirect,
} from "react-router-dom";

import Main from '../containers/Main';

import AuthActions from '../redux/redux-sauce/auth';


class ViberAuthenticationComponent extends Component {
    componentDidMount() {
      const { token } = this.props.match.params
      const { user } = this.props
      
      if (!user) {
        this.props.checkToken(token);
      }
    }

    render() {
      let access = false
      if (this.props.isLoggedIn === true) access = true
      return (
          <div>
              {access
                  ? <Redirect
                    to={{
                      pathname: "/",
                      state: { from: 'viber login' }
                    }}
                  />
                  : 'Не авторизован'
              }
          </div>
      )
    }
}

const mapStateToProps = (state) => ({
    isLoggedIn: state.auth.isLoggedIn,
    user: state.auth.user,
    state: state
});


const mapDispatchToProps = dispatch => ({
    checkToken: (token) => dispatch(AuthActions.checkTokenRequest(token)),
})

export default connect(mapStateToProps, mapDispatchToProps)(ViberAuthenticationComponent);

