import { createReducer, createActions } from 'reduxsauce'
import Immutable from 'seamless-immutable'
import { toggleArrayDictById, getObjectbyId } from '../../components/utils'


/* ------------- Types and Action Creators ------------- */
const { Types, Creators } = createActions({
    getJobsRequest: ['payload'],
    getJobsFail: ['payload'],
    getJobsSuccess: ['payload'],

    getJobDetailRequest: ['payload'],
    getJobDetailFail: ['error'],
    getJobDetailSuccess: ['payload'],

    getCategoriesRequest: ['payload'],
    getCategoriesFail: ['payload'],
    getCategoriesSuccess: ['payload'],

    createFirstJobRequest: ['payload'],
    createFirstJobFail: ['payload'],
    createFirstJobSuccess: ['payload'],

    partialUpdateJobRequest: ['payload'],
    partialUpdateJobFail: ['payload'],
    partialUpdateJobSuccess: ['payload'],

    activateDeactivateJobRequest: ['payload'],
    activateDeactivateJobFail: ['payload'],
    activateDeactivateJobSuccess: ['payload'],

    createJobRequest: ['payload'],
    createJobFail: ['payload'],
    createJobSuccess: ['payload'],

    addJobImageRequest: ['payload'],
    addJobImageFail: ['payload'],
    addJobImageSuccess: ['payload'],

    deleteJobImageRequest: ['payload'],
    deleteJobImageFail: ['payload'],
    deleteJobImageSuccess: ['payload'],
})

export const JobsTypes = Types
export default Creators


/* ------------- Initial State ------------- */
export const INITIAL_STATE = Immutable({
    listFetching: false,
    jobs: [],
    categories: [],
    errorList: null,

    detailFetching: null,
    jobDetail: null,
    errorDetail: null,

    eventFetching: null,

    message: '',
})


/* ------------- Selectors ------------- */
export const JobsSelectors = {
    getJobs: state => state.jobs.job_list,
    getCategories: state => state.jobs.categories,
    createJob: state => state.jobs.jobDetail,
}


/* ------------- Reducers ------------- */
// Get Jobs
export const getJobsRequest = (state, { payload }) => {
    return state.merge({ listFetching: true, jobs: [] })
}

export const getJobsSuccess = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList: null, jobs: payload.results,
         categories: payload.categories })
}

export const getJobsFail = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList: payload, jobs: [] })
}

// Get one
export const getJobDetailRequest = (state, { payload }) => {
    return state.merge({ detailFetching: true, })
}

export const getJobDetailSuccess = (state, { payload }) => {
    return state.merge({ detailFetching: false, errorDetail: null, jobDetail: payload})
}

export const getJobDetailFail = (state, { error }) => {
    return state.merge({ detailFetching: false, errorDetail: error, jobDetail: null })
}

// Get categories
export const getCategoriesRequest = (state, { payload }) => {
    return state.merge({ listFetching: true, categories: [] })
}

export const getCategoriesSuccess = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList: null, categories: payload })
}

export const getCategoriesFail = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList:payload, categories: [] })
}

// create first job
export const createFirstJobRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const createFirstJobSuccess = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList: null, message: payload.message })
}

export const createFirstJobFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList:payload, message: '' })
}

// partial update job
export const partialUpdateJobRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const partialUpdateJobSuccess = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList: null, message: payload.message })
}

export const partialUpdateJobFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList:payload, message: '' })
}

// activateDeactivate job
export const activateDeactivateJobRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const activateDeactivateJobSuccess = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList: null, message: payload.message })
}

export const activateDeactivateJobFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList:payload, message: '' })
}

// create job
export const createJobRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const createJobSuccess = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList: null, message: payload.message, 
        jobDetail: payload.job })
}

export const createJobFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList:payload, message: '' })
}

// add job image
export const addJobImageRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const addJobImageSuccess = (state, { payload }) => {
    const jobInList = getObjectbyId(state.jobs, payload.job.id)
    const index = state.jobs.indexOf(jobInList)
    return state.merge({ eventFetching: false, errorList: null, message: payload.message, 
        jobs: [...state.jobs.slice(0, index), payload.job, ...state.jobs.slice(index + 1)]
         })
}

export const addJobImageFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList:payload, message: '' })
}

// delete job image
export const deleteJobImageRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const deleteJobImageSuccess = (state, { payload }) => {
    const jobInList = getObjectbyId(state.jobs, payload.job.id)
    const index = state.jobs.indexOf(jobInList)
    return state.merge({ eventFetching: false, errorList: null, message: payload.message,
        jobs: [...state.jobs.slice(0, index), payload.job, ...state.jobs.slice(index + 1)]
     })
}

export const deleteJobImageFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList:payload, message: '' })
}

/* ------------- Hookup Reducers To Types ------------- */

export const reducer = createReducer(INITIAL_STATE, {
    [Types.GET_JOBS_REQUEST]: getJobsRequest,
    [Types.GET_JOBS_SUCCESS]: getJobsSuccess,
    [Types.GET_JOBS_FAIL]: getJobsFail,

    [Types.GET_JOB_DETAIL_REQUEST]: getJobDetailRequest,
    [Types.GET_JOB_DETAIL_SUCCESS]: getJobDetailSuccess,
    [Types.GET_JOB_DETAIL_FAIL]: getJobDetailFail,

    [Types.GET_CATEGORIES_REQUEST]: getCategoriesRequest,
    [Types.GET_CATEGORIES_SUCCESS]: getCategoriesSuccess,
    [Types.GET_CATEGORIES_FAIL]: getCategoriesFail,

    [Types.CREATE_FIRST_JOB_REQUEST]: createFirstJobRequest,
    [Types.CREATE_FIRST_JOB_SUCCESS]: createFirstJobSuccess,
    [Types.CREATE_FIRST_JOB_FAIL]: createFirstJobFail,

    [Types.PARTIAL_UPDATE_JOB_REQUEST]: partialUpdateJobRequest,
    [Types.PARTIAL_UPDATE_JOB_SUCCESS]: partialUpdateJobSuccess,
    [Types.PARTIAL_UPDATE_JOB_FAIL]: partialUpdateJobFail,

    [Types.ACTIVATE_DEACTIVATE_JOB_REQUEST]: activateDeactivateJobRequest,
    [Types.ACTIVATE_DEACTIVATE_JOB_SUCCESS]: activateDeactivateJobSuccess,
    [Types.ACTIVATE_DEACTIVATE_JOB_FAIL]: activateDeactivateJobFail,

    [Types.CREATE_JOB_REQUEST]: createJobRequest,
    [Types.CREATE_JOB_SUCCESS]: createJobSuccess,
    [Types.CREATE_JOB_FAIL]: createJobFail,

    [Types.ADD_JOB_IMAGE_REQUEST]: addJobImageRequest,
    [Types.ADD_JOB_IMAGE_SUCCESS]: addJobImageSuccess,
    [Types.ADD_JOB_IMAGE_FAIL]: addJobImageFail,

    [Types.DELETE_JOB_IMAGE_REQUEST]: deleteJobImageRequest,
    [Types.DELETE_JOB_IMAGE_SUCCESS]: deleteJobImageSuccess,
    [Types.DELETE_JOB_IMAGE_FAIL]: deleteJobImageFail,
})
