import React, { Component } from 'react';
import { Field, reduxForm } from 'redux-form'

import TextField from '@material-ui/core/TextField'
import Checkbox from '@material-ui/core/Checkbox'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import InputLabel from '@material-ui/core/InputLabel'
import FormHelperText from '@material-ui/core/FormHelperText'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'

import { SubmissionError } from 'redux-form'


const validateJobForm = values => {
  const errors = {}
  const requiredFields = [
    'title',
    'budget',
    'category',
    'phone',
    'nickname'
  ]
  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = 'Обязательное поле'
    }
  })
  return errors
}

const required = value => value ? undefined : 'Required'

const renderFromHelper = ({ touched, error }) => {
  if (!(touched && error)) {
    return
  } else {
    return <FormHelperText>{touched && error}</FormHelperText>
  }
}

const renderTextField = ({
  label,
  placeholder,
  input,
  multiline,
  meta: { touched, invalid, error },
  labelClass,
  ...custom
}) => (
  <TextField
    label={label}
    placeholder={placeholder}
    error={touched && invalid}
    helperText={touched && error}
    {...input}
    {...custom}
    InputLabelProps={{
      className: labelClass,
    }}
  />
)

const renderSelectField = ({
  input,
  label,
  formClass,
  labelClass,
  meta: { touched, error },
  children,
  ...custom
}) => (
  <FormControl error={touched && error} className={formClass}>
    <InputLabel className={labelClass}>{label}</InputLabel>
    <Select
      {...input}
      {...custom}
    >
      {children}
    </Select>
    {renderFromHelper({ touched, error })}
  </FormControl>
)

const renderDateTimeField = ({
  label,
  placeholder,
  input,
  meta: { touched, invalid, error },
  labelClass,
  ...custom
}) => (

  <TextField
    label={label}
    type="date"
    error={touched && invalid}
    helperText={touched && error}
    {...input}
    {...custom}
    InputLabelProps={{
      className: labelClass,
      shrink: true 
    }}
  />
)

function JobForm2 (props) {
  let {  categories, jobDate } = props
  return (
    <div>
        <Field
          component={renderTextField}
          name="title"
          label='Что нужно сделать?*'
          className='input-group my-2'
          labelClass='font-12'
          // validate={[ required ]}
        />

        <Field 
          name="category" 
          component={renderSelectField}
          labelClass='font-12 '
          formClass='input-group my-2'
          label='Выберите категорию работ*'
         >
            {categories && categories.length > 0 && categories.map(category => 
              <option className='font-10' value={category.id} key={category.id}>{category.ru_name}</option>
              )}
        </Field>
          
        <Field name="description" component="textarea" className="form-control font-12 my-2"
          style={{"height": "150px"}} label='Укажите объем и виды работ'
          placeholder='Какую работу нужно сделать? 
          - Напишите список работ.
          - Укажите объем работы в квадратных метрах, штуках или в другой подходящей единице измерения.
          - Опишите ваши пожелания и требования, если они есть.
          '
          />
        <FormControl className='input-group my-2'>
          <InputLabel className='font-12'>Когда нужно выполнить работу?</InputLabel>
          <Select onChange={props.setData} name='jobDate' className='input-group font-12'>
            <option value={'any'}>В любое время</option>
            <option value={'period'}>В период</option>
            <option value={'not later'}>Не позднее, чем</option>
          </Select>
        </FormControl>
        {jobDate == 'period' && 
          <div>
            <Field
              name="start_date"
              component={renderDateTimeField}
              label="C"
            />
            <Field
              name="end_date"
              component={renderDateTimeField}
              label="До"
            />
          </div>
          }
        {jobDate == 'not later' && 
          <Field
            name="end_date"
            component={renderDateTimeField}
          />
        }
        
        <Field
          name="budget"
          component={renderTextField}
          type="number"
          label="Бюджет до*"
          className='input-group my-2'
          labelClass='font-12'
        />

        <Field
          component={renderTextField}
          name="address"
          type="text"
          value={'Улан-Удэ'}
          label='Адрес'
          className='input-group my-2'
          labelClass='font-12'
        />
        
    </div>
  )
}

export class JobCreateForm extends Component {
  constructor(props) {
   super(props);
   this.state = {
     jobDate: 'any',
   };

   this.setData = this.setData.bind(this);   
 }

 componentDidMount() {
  const { start_date, end_date } = this.props.initialValues
  if (start_date) {
    this.setState({...this.state, jobDate : 'period'})
  }
  if (end_date && !start_date){
    this.setState({...this.state, jobDate : 'not later'})
  }
}

  setData (e) {
    this.setState({
      [e.target.name]: e.target.value
      })
  }
  
  render () {
    let { handleSubmit, pristine, reset, submitting, categories, job } = this.props
    return (
      <form onSubmit={handleSubmit(this.props.parentSubmit)}>
        <JobForm2 
          handleSubmit={handleSubmit}
          pristine={pristine}
          reset={reset}
          submitting={submitting}
          categories={categories}
          setData={this.setData}
          job={job}
          jobDate={this.state.jobDate} />
        <div>
          <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
              bg-blue2-light text-wrap'
            disabled={pristine || submitting}
            type="submit"
            >
            Создать
          </button>
        </div>
      </form>
    )
  }
}

JobCreateForm = reduxForm({
  form: 'jobCreate', // a unique identifier for this form
  validate: validateJobForm,
})(JobCreateForm)


