function UsernamePhoneForm () {
  return (
    <div>
      <Field
          component={renderTextField}
          name="nickname"
          label='Как к вам обращаться?'
          className='input-group my-2'
          labelClass='font-12'
        />
      <Field
          component={renderTextField}
          name="phone"
          label='Телефон'
          className='input-group my-2'
          labelClass='font-12'
        />
      <Field
        component={renderTextField}
        name="id"
        hidden={true}
      />
    </div>
  )
}

export function UsernamePhoneFormFull (props) {
  const { edit, setEdit, handleSubmit, initialValues } = props
  
  function clickSave() {
    setEdit(!edit);
    props.parentSubmit()
  }
  return (
    <form onSubmit={handleSubmit(clickSave)}>
      <UsernamePhoneForm initialValues={initialValues}/>
      <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
          bg-blue2-light text-wrap'
        type='submit'
        disabled={props.pristine || props.submitting}>
        Сохранить
      </button>
      <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
          bg-blue2-light text-wrap float-right'
        onClick={() => setEdit(!edit)}
      >
        назад
      </button>
    </form>
  )
}

UsernamePhoneFormFull = reduxForm({
  form: 'usernamePhoneFormFull', // a unique identifier for this form
  validate: validateJobForm,
})(UsernamePhoneFormFull)


