import { takeEvery, all, takeLatest } from "redux-saga/effects";

/* -------------- API -------------- */
import AuthApi from "../api/authApi";
import JobsApi from '../api/jobsApi';
import IsubsApi from '../api/isubsApi';

/* ------------- Types ------------- */
import { AuthTypes } from "../redux-sauce/auth";
import { JobsTypes } from '../redux-sauce/jobs';
import { IsubsTypes } from '../redux-sauce/isubs';

/* ------------- Sagas ------------- */
import { logIn, logOut, checkToken, signUp, checkAuth, partialUpdateUser } from "./authSagas";
import * as jobsSaga from './jobsSagas';
import * as isubsSaga from './isubsSagas';

const authApi = AuthApi.create();
const jobsApi = JobsApi.create();
const isubsApi = IsubsApi.create();

export default function* root() {
  yield all([
    takeEvery(AuthTypes.LOGIN_REQUEST, logIn, authApi),
    takeEvery(AuthTypes.LOGOUT_REQUEST, logOut, authApi),
    takeEvery(AuthTypes.CHECK_TOKEN_REQUEST, checkToken, authApi),
    takeEvery(AuthTypes.PARTIAL_UPDATE_USER_REQUEST, partialUpdateUser, authApi),

    takeEvery(JobsTypes.GET_JOBS_REQUEST, jobsSaga.getJobs, jobsApi),
    takeEvery(JobsTypes.GET_JOB_DETAIL_REQUEST, jobsSaga.getJobDetail, jobsApi),
    takeEvery(JobsTypes.GET_CATEGORIES_REQUEST, jobsSaga.getCategories, jobsApi),
    takeEvery(JobsTypes.CREATE_FIRST_JOB_REQUEST, jobsSaga.createFirstJob, jobsApi),
    takeEvery(JobsTypes.PARTIAL_UPDATE_JOB_REQUEST, jobsSaga.partialUpdateJob, jobsApi),
    takeEvery(JobsTypes.ACTIVATE_DEACTIVATE_JOB_REQUEST, jobsSaga.activateDeactivateJob, jobsApi),
    takeEvery(JobsTypes.CREATE_JOB_REQUEST, jobsSaga.createJob, jobsApi),
    takeEvery(JobsTypes.ADD_JOB_IMAGE_REQUEST, jobsSaga.addJobImage, jobsApi),
    takeEvery(JobsTypes.DELETE_JOB_IMAGE_REQUEST, jobsSaga.deleteJobImage, jobsApi),

    takeEvery(IsubsTypes.CREATE_OR_CHANGE_ISUB_REQUEST, isubsSaga.createOrChangeIsub, isubsApi),
  ]);
}
