import React, { Component } from 'react';
import { connect } from 'react-redux';
import { toggleArray } from '../components/utils'

// actions
import JobsActions from '../redux/redux-sauce/jobs';


export function JobBlock (props) {
  const { job, thumb, non_zoom } = props
  return (
    <div className='card card-style my-2'>
      <div className="content text-left">
          <h4 className='font-16 font-500 float-left'>{job.title}</h4>
          <h4 className='font-18 float-right'>{job.budget}</h4>
          <div className="clearfix"></div>
          <p className='text-left float-left mb-0 font-12 font-500'>{job.zakazchik}</p>
          <p className='text-left float-right mb-0 font-11 font-400 font-italic'>{job.category}</p>
          <div className="clearfix"></div>
          <p className="text-left mb-0">
            {job.description}
          </p>
          <p className='text-left float-left mb-0'>{job.address}</p>
          {job.start_date ? 
              <p className='text-left float-right mb-0'>c {job.start_date} до {job.end_date}</p>
              :
              job.end_date ?
                <p className='text-left float-right mb-0'>До {job.end_date}</p>
                :
                <p className='text-left float-right mb-0'>В любое время</p>
            }
          <div className="clearfix"></div>
          <p className='text-left float-left font-10 mb-1'>{job.created_ago}</p>
          <div className="clearfix"></div>
          {job.zakazchik_phone && <p className='font-16 mb-1'>{job.zakazchik_phone}</p>}

          {job.images.length > 0 && non_zoom && job.images.map(img => 
            <img src={'https://svoyaeda.su' + img.non_zoom_image} className='rounded-m my-1 mx-3'/>
            )}
          {job.images.length > 0 && thumb &&
            <div className='float-right mb-2'>
              {job.images.map(img => 
                <img src={'https://svoyaeda.su' + img.thumb_image} className='rounded-s mx-1' /> 
              )}
            </div>
          }
      </div>
    </div>
  )
}


class JobDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      needToRefresh: false,
    }
  }

  componentDidMount() {
    const { id } = this.props.match.params
    this.props.getJobDetail(id)
  }

  render() {
    const { jobDetail, detailFetching } = this.props.state.jobs
    return (
        jobDetail 
            ? <JobBlock job={jobDetail} non_zoom={true}/>
            : 'Не найдено'
    );
  }
}

const mapStateToProps = (state) => ({
  notifications: state.notifications,
  state: state,
  filterCategories: state.isubs.filterCategories
})

const mapDispatchToProps = (dispatch) => ({
  getCategories: query => dispatch(JobsActions.getCategoriesRequest(query)),
  getJobDetail: query => dispatch(JobsActions.getJobDetailRequest(query)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(JobDetail);
