import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Redirect } from "react-router-dom";

import { JobFirstCreateForm } from '../components/JobForms';
import { SubmissionError } from 'redux-form'

// actions
import JobsActions from '../redux/redux-sauce/jobs';
import AuthActions from '../redux/redux-sauce/auth';


class JobFirstCreateShop extends Component {
  constructor(props) {
    super(props);  
    this.state = {
    }

  this.submitCreateShop = this.submitCreateShop.bind(this);
  }

  componentDidMount() {
    this.props.getCategories()
  }

  submitCreateShop(e){
    this.props.createFirstJob(this.props.state.form.jobFirstCreate.values)
    const token = localStorage.getItem('token');
    if (token) {
      this.props.checkToken(token);
    }
    this.props.history.push("/zakazchik/account/")
  }

  render() {
    const { user } = this.props
    const { categories } = this.props.state.jobs
    return (
      user && user.zakazchik ?
      <Redirect
            to={{pathname: "/zakazchik/account/", }}
          />
      : user && categories.length > 1 &&
        <JobFirstCreateForm 
          parentSubmit={this.submitCreateShop}
          user={user} 
          categories={categories}
          initialValues={{
            nickname: user ? user.nickname : null,
            phone: user ? user.phone : null, id: user ? user.id : null,
            address: 'Улан-Удэ'
            }}
          />
    );
  }
}

const mapStateToProps = (state) => ({
  state: state,
  routing: state.routing,
  user: state.auth.user
})

const mapDispatchToProps = (dispatch) => ({
  createFirstJob: id => dispatch(JobsActions.createFirstJobRequest(id)),
  getCategories: id => dispatch(JobsActions.getCategoriesRequest(id)),
  checkToken: (token) => dispatch(AuthActions.checkTokenRequest(token)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(JobFirstCreateShop);
