import React, { Component, useState } from 'react';
import { connect } from 'react-redux';
import { JobCreateForm, JobUpdateForm, UsernamePhoneFormFull } from '../components/JobForms';

// actions
import JobsActions from '../redux/redux-sauce/jobs';
import AuthActions from '../redux/redux-sauce/auth';


function JobEditable (props) {
  const { job, jobEditId, categories } = props
  let category = null
  Object.keys(categories).forEach(key => {
    if (categories[key] == job.category)
      category = key
  })
  let edit = false
  if (job.id == jobEditId) edit = true

  return (
    <div>
      {edit ?
        <div className="card card-style mb-3 mx-0">
          <div className="content">
            <JobUpdateForm 
              categories={categories}
              parentSubmit={props.clickEditDone}
              job={job}
              initialValues={{
                id: job.id,
                title: job.title, category: category, description: job.description,
                start_date: job.start_date, end_date: job.end_date, budget: job.budget,
                address: job.address
              }}
              clickJobEdit={props.clickJobEdit}
              />
            </div>
        </div>
        :
        <div className='card card-style my-2 mx-0 pb-3'>
          <div className="content text-left">
            <h4 className='font-16 font-500 float-left'>{job.title}</h4>
            <h4 className='font-18 float-right'>{job.budget}</h4>
            <div className="clearfix"></div>
            <p className='text-left float-left mb-0 font-12 font-500'>{job.zakazchik}</p>
            <p className='text-left float-right mb-0 font-11 font-500'>{job.category}</p>
            <div className="clearfix"></div>
            <p className="text-left mb-0">
              {job.description}
            </p>
            <p className='text-left float-left mb-0'>{job.address}</p>
            {job.start_date ? 
                <p className='text-left float-right mb-0'>c {job.start_date} до {job.end_date}</p>
                :
                job.end_date ?
                  <p className='text-left float-right mb-0'>До {job.end_date}</p>
                  :
                  <p className='text-left float-right mb-0'>В любое время</p>
              }
            <div className="clearfix"></div>
            <p className='text-left font-10 mb-1'>{job.created_ago}</p>
            <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
              bg-blue2-light text-wrap' data-id={job.id} onClick={props.clickJobEdit}>
                Изменить
            </button>
            <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
              bg-red2-light text-wrap float-right' data-id={job.id} onClick={props.clickDeactivate}>
                Удалить
            </button>
            <div className="clearfix"></div>
            {props.eventFetching 
              ? <p>Fetching</p>
              : <div className=''>
                  {job.images.length > 0 && job.images.map(image =>
                    <div className='rounded-m img-fluid mx-2 my-2 float-left'
                      key={image.id} style={{"height": "75px"}}
                      >
                        <img src={'https://svoyaeda.su' + image.non_zoom_image} style={{"height": "75px"}}/>
                        <span className='badge badge-primary' 
                          onClick={() => props.deleteJobImage({image: image.id, id: job.id})}>
                          X
                        </span>
                      </div>
                    )}
                    <div className="clearfix"></div>
              </div>
            }
            <div className="file-data">
              <input className='upload-file bg-highlight shadow-s rounded-s bg-blue2-light'
                placeholder='Добавить фото' type='file' onChange={props.addJobImage} data-job-id={job.id} />
              <p className="upload-file-text color-white">Добавить фото</p>
            </div>
          </div>
        </div>
      }
    </div>
  )
}


class JobsEditable extends Component {
  constructor(props) {
    super(props);  
    this.state = {
      jobsEditList: [],
      jobEditId: null,
      needToRefresh: false
    }

  this.clickJobEdit = this.clickJobEdit.bind(this);
  this.clickEditDone = this.clickEditDone.bind(this);
  this.clickDeactivate = this.clickDeactivate.bind(this);
  this.clickCreateNew = this.clickCreateNew.bind(this);
  this.addJobImage = this.addJobImage.bind(this);
  }

  componentDidMount() {
  }

  clickJobEdit (e) {
    const { id } = e.target.dataset    
    this.setState({...this.state, 
      jobEditId: id
    })
  }

  clickEditDone () {
    const { values } = this.props.form.jobUpdate
    this.props.partialUpdateJob(values)
    this.setState({...this.state, 
      jobEditId: null,
      needToRefresh: true
    })
  }

  clickDeactivate (e) {
    const { id } = e.target.dataset  
    this.props.activateDeactivateJob({id: id, active: false})
    this.setState({...this.state, 
      jobEditId: null,
      needToRefresh: true
    })
  }

  clickCreateNew(e) {
    // e.preventDefault()
    const { values } = this.props.form.jobCreate
    this.props.createJob(values)
    this.setState({...this.state, 
      jobEditId: null,
      needToRefresh: true
    })
  }

  addJobImage(e) {
    this.props.addJobImage({original: e.target.files[0], id: e.target.dataset.jobId})
  }

  refreshSowsList () {
    if (!this.props.eventFetching && this.state.needToRefresh){
      setTimeout(() => {
        this.setState({...this.state, needToRefresh: false})
        this.props.getJobs({zakazchik: this.props.user.id})
      }, 10)
    }
  }

  render() {
    this.refreshSowsList()
    const { jobs, user, categories } = this.props
    const { jobEditId } = this.state
    return (
      <div className=''>
        {jobs.length > 0 && jobs.map(job => 
          <JobEditable 
            job={job}
            jobEditId={jobEditId}
            clickJobEdit={this.clickJobEdit} 
            clickEditDone={this.clickEditDone}
            clickDeactivate={this.clickDeactivate}
            categories={categories}
            key={job.id}
            addJobImage={this.addJobImage}
            deleteJobImage={this.props.deleteJobImage}
            eventFetching={this.props.eventFetching}
          />
        )}
        <div className="card card-style mb-3 mx-0">
          {jobs.length < 2 && user ? 
              <div className="content">
                <h3 className='font-18 font-500 text-center'>Создать работу</h3>
                <JobCreateForm 
                    categories={categories}
                    parentSubmit={this.clickCreateNew}
                    initialValues={{
                      zakazchik: user ? user.id : null, start_date: null, end_date: null, address: 'Улан-Удэ',
                    }}
                  />
              </div>
            :
            <p className='font-18 font-500 text-left'>
              У вас больше 2х активных работ.
            </p>
          }
        </div>
      </div>
    );
  }
}


function UsernamePhone (props) {
  const { user } = props
  const [ edit, setEdit] = useState(0);
  return (
    edit 
    ? <div className='card card-style mb-1 mt-2 rounded-m mx-0'>
        <div className="content">
          <UsernamePhoneFormFull
              parentSubmit={props.changeUsernamePhone}
              initialValues={{
                nickname: user ? user.nickname : null, phone: user.phone, id: user.id
              }}
              setEdit={setEdit}
              edit={edit}
          />
        </div>
      </div>
    : <div className='card card-style mb-1 mt-2 rounded-m mx-0'>
        <div className="content">
          <div className='row my-0'>
            <div className='col-8'>
              <h5 className='text-center font-17 font-600 mb-1'>{user.nickname}</h5>
              <h5 className='text-center font-17 font-600'>{user.phone}</h5>
            </div>
            <div className='col-4'>
              <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
                bg-blue2-light text-wrap float-right'
                onClick={() => setEdit(!edit)}
              >
                Изменить
              </button>
            </div>
          </div>
        </div>
      </div>
  )
}


class ZakazchikAcc extends Component {
  constructor(props) {
    super(props);  
    this.state = {
    }

  this.submitCreateShop = this.submitCreateShop.bind(this);
  this.changeUsernamePhone = this.changeUsernamePhone.bind(this);
  }

  componentDidMount() {
    const user_id = localStorage.getItem('user_id');
    this.props.getJobs({zakazchik: user_id})
  }

  submitCreateShop(e){
    e.preventDefault()
    this.props.createFirstJob(this.props.state.form.jobCreate.values)
  }

  changeUsernamePhone (e) {
    this.props.updateUser(this.props.state.form.usernamePhoneFormFull.values)
  }

  render() {
    let { user } = this.props
    const { categories, jobs } = this.props.state.jobs
    const { listFetching } = this.props.state.jobs
    return (
      <div className='container-fluid shop-create'>
        {user && <UsernamePhone user={user} changeUsernamePhone={this.changeUsernamePhone}/>}
        {listFetching ?
          <p>Loading</p>
          :
          <JobsEditable 
            user={user} 
            form={this.props.form}
            categories={categories}
            eventFetching={this.props.state.jobs.eventFetching}
            jobs={jobs}
            getJobs={this.props.getJobs}
            partialUpdateJob={this.props.partialUpdateJob}
            activateDeactivateJob={this.props.activateDeactivateJob}
            createJob={this.props.createJob}
            addJobImage={this.props.addJobImage}
            deleteJobImage={this.props.deleteJobImage}
            />
        }
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  state: state,
  routing: state.routing,
  user: state.auth.user,
  jobs: state.jobs.jobs,
  form: state.form
})

const mapDispatchToProps = (dispatch) => ({
  createFirstJob: id => dispatch(JobsActions.createFirstJobRequest(id)),
  partialUpdateJob: id => dispatch(JobsActions.partialUpdateJobRequest(id)),
  activateDeactivateJob: id => dispatch(JobsActions.activateDeactivateJobRequest(id)),
  getJobs: query => dispatch(JobsActions.getJobsRequest(query)),
  createJob: id => dispatch(JobsActions.createJobRequest(id)),
  addJobImage: id => dispatch(JobsActions.addJobImageRequest(id)),
  deleteJobImage: id => dispatch(JobsActions.deleteJobImageRequest(id)),

  checkToken: (token) => dispatch(AuthActions.checkTokenRequest(token)),
  updateUser: id => dispatch(AuthActions.partialUpdateUserRequest(id)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ZakazchikAcc);
