import axios from 'axios';
import endpoints from './endpoints';
import { parseErrorData, createUrlParamsFromFilters } from './utils';


const create = () => {
  const logIn = (data: { username: string, password: string}): void => {
    const { username, password } = data;
    
     return axios.post(endpoints.JWT_AUTH, { username, password })
      .then(response => {
        if (response.status < 200 || response.status >= 300) {
            throw new Error(response);
        }
        return { token: response.data.token, user: response.data.user };
      })
      .catch(err => {
        throw new Error(err.response.data[Object.keys(err.response.data)[0]][0])
      })
      .then((response) => {
          // console.log('response.data', response.data)
          localStorage.setItem('token', response.data.token);
          return response.user;
      })
  }

  const logOut = () => {
    localStorage.removeItem('token');
  }

  const checkToken = (payload: string) => {
    return axios.post(endpoints.JWT_CHECK_TOKEN, { token: payload })
      .then(response => {
        localStorage.setItem('token', response.data.token);
        localStorage.setItem('user_id', response.data.profile.id);
        return response.data
      })
      .catch(err => {
        const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
      })
  }

  const partialUpdateUser = payload => {
    const { id, nickname, phone } = payload;
    const token = localStorage.getItem('token') || '';
    const url = endpoints.updateUser(id);

    const formData = new FormData();
    nickname && formData.append("nickname", nickname);
    phone && formData.append("phone", phone);

    return axios({
                method: 'patch',
                url: url,
                data: formData,
                headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
    })
    .then(response => {
        return response.data
    })
    .catch(err => {
        const error = new Error(err);
        error.data = parseErrorData(err);
        throw error;
    })
}

  return {
    logIn,
    checkToken,
    logOut,
    partialUpdateUser
  }
}

export default {
  create
}