import axios from 'axios';
import endpoints from './endpoints';
import { parseErrorData, createUrlParamsFromFilters } from './utils';

const create = () => {

    const getCategories = (filters) => {
        const params = createUrlParamsFromFilters(filters);
        return axios.get(endpoints.GET_CATEGORIES, { params })
        .then(response => response.data)
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const getJobs = (filters) => {
        const params = createUrlParamsFromFilters(filters);
        return axios.get(endpoints.GET_JOBS, { params })
        .then(response => response.data)
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const getJobDetail = id => {
        const token = localStorage.getItem('token') || '';
        const url = endpoints.getJobDetail(id);

        return axios({
                    method: 'get',
                    url: url,
                    // headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const createFirstJob = payload => {
        const { nickname, phone, title, description, budget, address, start_date, end_date, category } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.CREATE_FIRST_JOB;

        const formData = new FormData();
        nickname && formData.append("nickname", nickname);
        formData.append("phone", phone);

        formData.append("title", title);
        formData.append("budget", budget);
        formData.append("category", category);
        description && formData.append("description", description);
        address && formData.append("address", address);
        start_date && formData.append("start_date", start_date);
        end_date && formData.append("end_date", end_date);
        
        return axios({
                    method: 'post',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const partialUpdateJob = payload => {
        const { id, title, description, budget, address, start_date, end_date, category } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.getJobDetail(id);

        const formData = new FormData();
        title && formData.append("title", title);
        description && formData.append("description", description);
        budget && formData.append("budget", budget);
        address && formData.append("address", address);
        category && formData.append("category", category);
        start_date && formData.append("start_date", start_date);
        end_date && formData.append("end_date", end_date);
        
        return axios({
                    method: 'patch',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const activateDeactivateJob = payload => {
        const { id, active } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.activateDeactivateJobDetail(id);

        const formData = new FormData();
        formData.append("active", active);
        
        return axios({
                    method: 'post',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const createJob = payload => {
        const { title, description, budget, address, start_date, end_date, category } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.GET_JOBS;

        const formData = new FormData();
        formData.append("title", title);
        formData.append("description", description);
        formData.append("budget", budget);
        formData.append("address", address);
        formData.append("category", category);
        start_date && formData.append("start_date", start_date);
        end_date && formData.append("end_date", end_date);
        
        return axios({
                    method: 'post',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const addJobImage = payload => {
        const { id, original } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.addJobImage(id);

        const formData = new FormData();
        formData.append("original", original);
        
        return axios({
                    method: 'post',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const deleteJobImage = payload => {
        const { id, image } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.deleteJobImage(id);

        const formData = new FormData();
        formData.append("image", image);
        
        return axios({
                    method: 'post',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    return {
        getJobs,
        getCategories,
        getJobDetail,
        createFirstJob,
        partialUpdateJob,
        activateDeactivateJob,
        createJob,
        addJobImage,
        deleteJobImage
    }
}

export default {
    create
}