import { put, call } from 'redux-saga/effects';

import JobsActions from '../redux-sauce/jobs'

export function* getJobs(api, action) {
    try {
        let response = yield call(api.getJobs, action.payload);
        yield put(JobsActions.getJobsSuccess(response));
    } catch (err) {
        yield put(JobsActions.getJobsFail(err))
    }
}

