import React, { Component } from 'react';
import { connect } from 'react-redux';
import { toggleArray } from '../components/utils'
import {
  Link,
} from "react-router-dom";

import { JobBlock } from './JobDetail';

// actions
import JobsActions from '../redux/redux-sauce/jobs';
import IsubsActions from '../redux/redux-sauce/isubs';


function Categories (props){
  const { categories } = props
  let catClass = "btn btn-xs mb-1 ml-1 rounded-xl text-uppercase font-900 shadow-s \
     text-wrap float-left"
  let active = " bg-red2-light"
  let notActive = " bg-blue2-light"
  return (
    <div>
      {categories.length > 0 && categories.map(category => 
          <button 
            className={props.filterCategories.includes(category.id) ? catClass + active : catClass + notActive }
            key={category.id} onClick={props.clickCategory} 
            data-id={category.id}
            >
              {category.ru_name}
          </button>
        )}
      <div className="clearfix"></div>
    </div>
  )
}

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      needToRefresh: false,
    }
    this.clickCategory = this.clickCategory.bind(this);
    this.clickSubscribe = this.clickSubscribe.bind(this);
    this.clickUnsubscribe = this.clickUnsubscribe.bind(this);
    this.clickSubscribeAll = this.clickSubscribeAll.bind(this);
  }

  componentDidMount() {
    this.props.getJobs()
  }

  clickCategory(e) {
    const { id } = e.target.dataset
    this.props.toggleFilterCategories(id)
    this.setState({...this.state, needToRefresh: true})
  }

  clickSubscribe(){
    this.props.createOrChangeIsub({categories: this.props.filterCategories})
  }

  clickSubscribeAll(){
    let categories = []
    this.props.state.jobs.categories.map(cat => categories = toggleArray(categories, cat.id))
    this.props.createOrChangeIsub({categories: categories})
  }

  clickUnsubscribe(){
    this.props.createOrChangeIsub({})
  }

  refreshList () {
    if (!this.props.state.jobs.listFetching && this.state.needToRefresh){
      setTimeout(() => {
        this.setState({...this.state, needToRefresh: false})
        const { filterCategories } = this.props
        this.props.getJobs({category: filterCategories})
      }, 10)
    }
  }

  getGreetings(){
    var greetingTime = new Date().getHours();
    var greetingMessage;
    var greetingMorning = ('Доброе утро');
    var greetingAfternoon = ('Добрый день');
    var greetingEvening = ('Добрый вечер');
    if (greetingTime >= 0 && greetingTime < 12) {greetingMessage = greetingMorning; 
    } else if (greetingTime >= 12 && greetingTime < 17) {greetingMessage = greetingAfternoon;
    } else if (greetingTime >= 17 && greetingTime < 24) {greetingMessage = greetingEvening;}

    return greetingMessage
  }

  render() {
    this.refreshList()
    const { jobs, categories, listFetching, errorList } = this.props.state.jobs
    const { filterCategories, isub } = this.props.state.isubs
    let { user } = this.props.state.auth
    const greetingMessage = this.getGreetings()
    return (
        <div>
          <div className="page-title page-title-large">
            <h2 style={{"color": '#fff'}}>{greetingMessage}, <br/>{user ? user.nickname : 'Пользователь'}</h2>
            <img data-menu="menu-main" src={user ? user.viber_avatar : 'images/pictures/20s.jpg'}
              className="bg-fade-gray1-dark shadow-xl" 
            />
          </div>
          <div className="card header-card shape-rounded" data-card-height="210" style={{"height": "210px"}}>
            <div className="card-overlay bg-highlight opacity-95"></div>
            <div className="card-overlay dark-mode-tint"></div>
            <div className="card-bg preload-img" data-src="images/pictures/20s.jpg" 
              style={{"backgroundImage": "url('images/pictures/20s.jpg')"}}></div>
          </div>
          <div className="card card-style mb-3">
            <div className="content">
              <Categories 
                categories={categories} 
                getJobs={this.props.getJobs} 
                filterCategories={filterCategories}
                toggleFilterCategories={this.props.toggleFilterCategories}
                clickCategory={this.clickCategory}
              />
              {user && categories && isub && isub.categories.length > 0 &&
                <div>
                  <p className='my-2 mr-1 ml-3 font-11 text-wrap font-italic' style={{"lineHeight": "95%"}}>
                    Вы подписаны на {isub.categories.map((cat, idx) =>
                      cat.ru_name + ', '
                    )}
                  </p>
                  <button className='btn btn-m mb-1 ml-1 rounded-xl text-uppercase font-900
                      shadow-s bg-red2-light float-right' onClick={this.clickUnsubscribe}>
                        Отписаться
                  </button>
                </div>
              }
              {filterCategories.length > 0 
                ? user 
                  ? <button className='btn btn-m mb-1 ml-1 rounded-xl text-uppercase font-900
                      shadow-s bg-blue1-dark float-left' onClick={this.clickSubscribe}>
                      Подписаться ({filterCategories.length})
                    </button>
                  : <p>Вы можете получать сообщения о новых работах на вайбер, если авторизуетесь.</p>
                : isub && isub.categories < 1 && 
                  <button className='btn btn-m mb-1 ml-1 rounded-xl text-uppercase font-900
                    shadow-s bg-blue1-dark float-left' onClick={this.clickSubscribeAll}>
                    Подписаться на все категории
                  </button>
              }
            </div>
          </div>
          {listFetching && <p>Loading...</p>}
          {jobs.length > 0 && jobs.map(job =>
            <Link to={`/jobs/${job.id}/`}>
              <JobBlock job={job} key={job.id} thumb={true}/>
            </Link>
            )}
        </div>
    );
  }
}

const mapStateToProps = (state) => ({
  notifications: state.notifications,
  state: state,
  filterCategories: state.isubs.filterCategories
})

const mapDispatchToProps = (dispatch) => ({
  getCategories: query => dispatch(JobsActions.getCategoriesRequest(query)),
  getJobs: query => dispatch(JobsActions.getJobsRequest(query)),

  toggleFilterCategories: catId => dispatch(IsubsActions.toggleFilterCategories(catId)),
  createOrChangeIsub: catId => dispatch(IsubsActions.createOrChangeIsubRequest(catId)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Main);
